import React, { useState, useEffect, useRef } from "react";

const CircleProgressBar: React.FC = () => {
  const [progress, setProgress] = useState(0);
  const containerRef = useRef<HTMLDivElement | null>(null);

  const handleScroll = () => {
    if (containerRef.current) {
      const containerHeight = containerRef.current.offsetHeight - 100;
      const containerTop = containerRef.current.offsetTop;
      const containerBottom = containerTop + containerHeight;
      const windowHeight = window.innerHeight;
      const scrollTop = Math.max(
        window.scrollY - containerTop + windowHeight,
        0
      );

      const percentage = Math.round(
        ((scrollTop - windowHeight) /
          (containerBottom - containerTop - windowHeight)) *
          100
      );

      setProgress(Math.max(Math.min(percentage, 100), 0));
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);
  const radius = 45;
  const strokeWidth = 1;
  const dpr = window.devicePixelRatio || 1;
  const circumference = 2 * Math.PI * radius;
  const strokeDasharray = circumference * dpr;

  const strokeDashoffset = Math.max(
    circumference - (progress / 100) * circumference,
    0
  );
  // const renderText = (value: number, nextValue: number) => {
  //   return progress >= value && progress < nextValue ? (
  //     <p>{`${value}%`}</p>
  //   ) : null;
  // };

  const renderText = (value: number, nextValue: number) => {
    // let text: string | null = null;
    let text = "";
    if (progress >= value && progress < nextValue) {
      if (value === 10) {
        text = "Alter Link  ";
      } else if (value === 15) {
        text =
          'Alter Link    Pan-gaea  .<br />"PV - Pan-gaea  Alter Link NFT       .<br />  NFT     .<br />        NFT   .';
      } else if (value === 20) {
        text = "   ";
      } else if (value === 30) {
        text = "100%  , ";
      } else if (value === 35) {
        text = "      ";
      } else if (value === 40) {
        text = "Alter Link NFT   , ";
      } else if (value === 45) {
        text = "Alter Link NFT  ";
      } else if (value === 50) {
        text = "   Alter Link NFT  ";
      } else if (value === 55) {
        text = "Alter Link NFT  11  ";
      } else if (value === 60) {
        text = "     ";
      } else if (value === 65) {
        text = "Alter Link NFT   ";
      } else if (value === 70) {
        text = "  NFT ";
      } else if (value === 75) {
        text = "NFT   ";
      } else if (value === 80) {
        text = "  ,  ";
      } else if (value === 90) {
        text = "   ,";
      }
    } else if (progress >= 100 && value === 90) {
      text = ",  .";
    }
    return text ? <p dangerouslySetInnerHTML={{ __html: text }}></p> : null;
  };
  return (
    <>
      <div id="section06">
        <div id="roadmap" ref={containerRef}>
          <div className="road_wrap">
            <div className="sec_inner">
              <img
                src={process.env.PUBLIC_URL + "/img/main/sec06_top_line.png"}
                alt=""
              />
              <img
                src={process.env.PUBLIC_URL + "/img/main/sec06_pattern.png"}
                alt=""
              />
            </div>
            <div className="circle_wrap">
              <div className="text-container">
                <h2> {`${Math.round(progress)}%`}</h2>
                {renderText(10, 15)}
                {renderText(15, 20)}
                {renderText(20, 30)}
                {renderText(30, 35)}
                {renderText(35, 40)}
                {renderText(40, 45)}
                {renderText(45, 50)}
                {renderText(50, 55)}
                {renderText(55, 60)}
                {renderText(60, 65)}
                {renderText(65, 70)}
                {renderText(70, 75)}
                {renderText(75, 80)}
                {renderText(80, 90)}
                {renderText(90, 100)}
              </div>
              <svg viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  strokeWidth={strokeWidth}
                  fill="transparent"
                />
                <circle
                  cx="50"
                  cy="50"
                  r="45"
                  strokeWidth={strokeWidth}
                  fill="transparent"
                  stroke="#fff"
                  strokeDasharray={circumference}
                  strokeDashoffset={strokeDashoffset}
                  transform="rotate(-90 50 50)"
                />
                {/* <text
                  x="50%"
                  y="50%"
                  dominantBaseline="middle"
                  textAnchor="middle"
                  fontSize="20px"
                  fill="#78e58f"
                >
                  {`${Math.round(progress)}%`}
                </text> */}
              </svg>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CircleProgressBar;
